import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';
import { Panel, Group, Div, Button, Header, List, ListItem, Avatar, Cell} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon24Privacy from '@vkontakte/icons/dist/24/privacy';

const User = props => (
  <Group style={{margin:0, pading: 0}}>
    <Div style={{ background: '#5C9CE6', color: '#fff' }}>
      ЛИЧНЫЙ КАБИНЕТ
    </Div>
    <Group title="Информация пользователя" style={{margin:0, pading: 0}}>
    {props.fetchedUser &&
        <ListItem
          before={<Avatar src={props.fetchedUser.photo_200}/>}
          description={props.fetchedUser.city.title}
        >
          {`${props.fetchedUser.first_name} ${props.fetchedUser.last_name}`}
        </ListItem>}
        <List>
          <Cell expandable before={<Icon24User />}>Мои записи</Cell>
          <Cell expandable before={<Icon24User />}>Отменить предзапись</Cell>
          <Cell expandable before={<Icon24Settings />}>Поменять имя в своем профиле</Cell>
          <Cell expandable before={<Icon24Settings />}>Отключить уведомления предзаписи</Cell>
          <Cell expandable before={<Icon24Privacy />}>Отозвать согласие на обработку персональных данных</Cell>
      </List>
      </Group>
  </Group>
);

User.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default User;


