import React from 'react';
import ReactDOM from 'react-dom';
import { View, Panel,Footer, PanelHeader, PanelHeaderContent, Div,platform, Button, FixedLayout, Tabs, TabsItem, IOS, ANDROID,getClassName } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Entry from '/home/ekaterina_a/mfc-service/src/components/Entry';
import User from '/home/ekaterina_a/mfc-service/src/components/User';

const osname = platform();

class Navigation extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeTab: 'entry',
      //fetchedUser: this.props.fetchedUser,
    };
  }

  renderSwitch(param) {
    switch(param) {
      case 'entry':
        return <Entry go={this.props.go}/>;
      case 'user':
        return <User fetchedUser={this.props.fetchedUser}/>
      default:
        console.log(this.state.activeTab);
      }
  }

  render() {
    return (
        <Panel id="navigation" >
          <PanelHeader  noShadow >
            <PanelHeaderContent
        status="Тюменская область"
      >
        Центр "Мои Документы"
      </PanelHeaderContent>
          </PanelHeader>
            {this.renderSwitch(this.state.activeTab)}
            {this.state.activeTab === 'entry' ?
              <Footer>Для того чтобы записаться на прием в центры госуслуг "Мои Документы"", введите название необходимой услуги или выберите ее из списка.</Footer>
            : ""}
          <FixedLayout vertical="bottom">
            <Tabs theme="light" >
              <TabsItem
                onClick={() => this.setState({ activeTab: 'entry'})}
                selected={this.state.activeTab === 'entry'}>
                Запись
              </TabsItem>
              <TabsItem
                onClick={() => this.setState({ activeTab: 'user'})}
                selected={this.state.activeTab === 'user'}>
                Личный кабинет
              </TabsItem>
            </Tabs>
          </FixedLayout>
        </Panel>
    );
  }
}

export default Navigation;

