import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { View, Panel,  PanelHeader, platform, FixedLayout, Tabs, TabsItem, IOS, ANDROID } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import connect from '@vkontakte/vkui-connect';

import Navigation from './panels/Navigation';
import Services from '/home/ekaterina_a/mfc-service/src/panels/Services';
import Entry from '/home/ekaterina_a/mfc-service/src/components/Entry';
import User from '/home/ekaterina_a/mfc-service/src/components/User';


const osname = platform();


class App extends React.Component {
  constructor(props) {
		super(props);

		this.state = {
      activePanel: 'navigation',
			fetchedUser: null,
		};
	}

  componentWillMount() {
    connect.subscribe((e) => {
      switch (e.detail.type) {
        case 'VKWebAppGetUserInfoResult':
          this.setState({ fetchedUser: e.detail.data });
          break;
        default:
          console.log(e.detail.type);
      }
    });
    connect.send('VKWebAppGetUserInfo', {});
  }

  go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};


  render() {
    return (
      <View activePanel={this.state.activePanel}>
				<Navigation id="navigation" fetchedUser={this.state.fetchedUser} go={this.go}/>
        <Services id="services" go={this.go}/>
			</View>
    );
  }
}

export default App;

