import React from 'react';
import ReactDOM from 'react-dom';
import { Panel, Group, Div, Button, Header, List, ListItem, Search, Cell, Footer} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';


const thematics = [
    {id: 2201, name: "Регистрация прав на имущество и постановка на кадастровый учет"},
    {id: 2273, name: "Выписка ЕГРН/копия правоустанавливающих документов"},
    {id: 2205, name: "Транспортная карта студенту, школьнику, пенсионеру"},
    {id: 2282, name: "Первичное получение/ замена паспорта РФ"},
    {id: 3288, name: "Прием дополнительных документов по Кадастру"},
  ];

const top_thematics = [
    {id: 3201, name: "Ведение бизнеса"},
    {id: 3273, name: "Выдача результата"},
    {id: 3205, name: "ГосУслуги"},
    {id: 3282, name: "Индивидуальное жилищное строительство"},
    {id: 3283, name: "Консультация"},
    {id: 3284, name: "Несовершеннолетние дети"},
    {id: 3285, name: "Перемена имени"},
    {id: 3286, name: "Получение/замена документов"},
    {id: 3287, name: "Получение и направление сведений"},
    {id: 3288, name: "Потеря или поиск работы"},
    {id: 3117, name: "Право на пенсию"},
    {id: 3118, name: "Приобретение/продажа объекта недвижимости"},
    {id: 3119, name: "Рождение ребенка"},
    {id: 3120, name: "Смена места жительства"},
    {id: 3121, name: "Услуги для отдельных категорий граждан"},
    {id: 3122, name: "Утрата близкого человека"}
  ];

class Entry extends React.Component {

  constructor (props) {
       super(props);
       this.state = {
         search: ''
       }
       this.onChange = this.onChange.bind(this);
  }

  onChange (search) { this.setState({ search }); }

  get thematics () {
     const search = this.state.search.toLowerCase();
     return thematics.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
  }

  render() {
    return (
      <Group style={{margin:0, pading: 0, background: '#ebedf0'}}>
        <Search value={this.state.search} onChange={this.onChange}/>
              <Div style={{ margin:0, pading: 0, background: '#5C9CE6', color: '#fff' }}>
                ПРЕДВАРИТЕЛЬНАЯ ЗАПИСЬ
              </Div>
              <Group  title="Услуги"style={{margin:0, pading: 0}}>
                  { this.thematics.length > 0 &&
                    <List style={{marginLeft:10, marginRight:10}}>
                      {this.thematics.map(thematic => <Cell multiline key={thematic.id}>{thematic.name}</Cell>)}
                    </List>
                  }
                  <Div>
                    <Button size="xl" level="2" onClick={this.props.go} data-to="services">
                      Список всех услуг
                    </Button>
                  </Div>
              </Group>
      </Group>
    );
  }
}

export default Entry;

